<script>
	import File from './Toc.File.svelte';

	export let expanded = false;
	export let name;
  export let files;
  export let href;

	function toggle() {
		expanded = !expanded;
  }
  function goUp(){alert("going up");}
</script>

<style>
	span {
		padding: 0 0 0 1.5em;
		background: url(../images/folder.svg) 0 0.1em no-repeat;
		background-size: 1em 1em;
		font-weight: bold;
		cursor: pointer;
	}

	
	.expanded {
		background-image: url(../images/folder-open.svg);
	}

	ul {
		padding: 0.2em 0 0 0.0em;
		margin: 0 0 0 0.0em;
		list-style: none;
		border-left: 1px solid #eee;
	}

	li {
		padding: 0.2em 0;
    margin-left:-0.1em; 
	}

</style>

<span class:expanded on:click={toggle}>{name} {"ã€€"}</span><svg on:click={goUp} style=" cursor: pointer; display:inline-block" xmlns='http://www.w3.org/2000/svg' viewBox='0 0 6.4 6.4' width='0.7em' height='0.7em' stroke="black" fill='none' >
<g>  <path d='M 0 0 L 0 6.4 L 6.4 6.4 ' /> </g>
<g stroke="black" fill='black'> <path d='M 0 0  L 3.2 3.2 L 0 3.2 Z ' /></g> 
</svg>
<!--&#x021C8;-->

{#if expanded}
	<ul>
		{#each files as file}
			<li>
				{#if file.type === 'folder'}
					<svelte:self {...file}/>
				{:else}
					<File {...file} href={file.href}/>
				{/if}
			</li>
		{/each}
