<script>
import {title} from '../js/HeaderStore.js'
export let asHeader=true

let title_v;
const unsubscribe = title.subscribe(value => {title_v = value;});

let thc=asHeader ? ["tl","","tr"] : ["bl","","br"]
let year=new Date().getFullYear()
const span10="<span>&#x0200A;</span>".repeat(12)
</script>
<style>svg {  
  margin-left:0px;
  margin-bottom:4px;
  display:inline;
  width:100%;
  height:1.75em ;
  }
  h1 { margin-top: -0.6em; }

  .spanF {margin-top: 10em;padding:5em 0 0 0;}
  </style>
  
  
{#if asHeader}

<h1 >{@html span10}{title_v}</h1>
<div class="grid grid-cols-8 gap-0 justify-center " >

{#each  thc as symbol}

{#if symbol==""}
<div class="col-span-2 text-center " ></div>

{:else}
<div class="col-span-3 text-center" > 
<svg role="img" >
 <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="images/HC.svg#{symbol}" transform="scale(7 7) translate(0 0) " ></use>
</svg>

  </div>
  {/if}
  
  {/each}

</div>





{:else}
	<div class="text-center">
  <h1>&#x0200A;</h1>
  
  </div>

<div class="grid grid-cols-8 gap-0 justify-center " >

{#each  thc as symbol}

{#if symbol==""}
<div class="col-span-2 text-center " style="margin-top:0.5em;"><span  >Math vs. IT {year}</span></div>

{:else}
<div class="col-span-3 text-center " > 
<svg role="img" >
 <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="images/HC.svg#{symbol}" transform="scale(7 7) translate(0 0) " ></use>
</svg>

  </div>
  {/if}
  
  {/each}

</div>
<br/>


{/if}

